import React from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import Login from '../components/Login';
import {sendLogin,isLoggedIn} from '../actions/LoginActions.js'

const LoginContainer = function (props) {
        
  
    console.log(props,'----')  
    return (
        <div>
            <Login sendLogin={props.sendLogin} isAuthenticated = {props.isAuthenticated} showLogout= {props.showLogout} ></Login>
        </div>
    )

}
function mapStateToProps(state){
    return {
        userName:state.userName
        ,isAuthenticated: state.isAuthenticated
    }
}
function mapDispatchToProps(dispatch){
    return {
        ...bindActionCreators({sendLogin,isLoggedIn},dispatch)
        }
}

export default connect(mapStateToProps,mapDispatchToProps)(LoginContainer)